    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }


@keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
#return-to-top {
    position: fixed;
    bottom: 20px;
    right: 20px;
    background: rgb(0, 0, 0);
    background: rgba(0, 0, 0, 0.7);
    width: 50px;
    height: 50px;
    display: block;
    text-align: center;
    text-decoration: none;
    border-radius: 35px;
    /* display: none; */
    transition: all 0.3s ease;
    cursor: pointer;
}
#return-to-top strong {
    color: #fff;
    margin: 0;
    position: relative;
    /* left: 16px; */
    top: 13px;
    font-size: 19px;
    -webkit-text-decoration: wavy;
            text-decoration: wavy;
    transition: all 0.3s ease;
}
#return-to-top:hover {
    background: rgba(0, 0, 0, 0.9);
}
#return-to-top:hover strong {
    color: #fff;
    top: 5px;
}
